<template lang="html">
<div>
<div class="content" id="content" ref="viewBox">
    <div class="winner">
        <div class="winner-detail wrap">
        <div class="title">
            <div class="title-left flex-1">
                <span class="title-icon"><em></em></span>{{$t('m.winnerIndex.num')}} <span class="num">{{round}} </span>
            </div>
            <div class="title-right">
                <div class="tit">{{$t('m.winnerIndex.contract_address')}}</div>
                <div class="info"><span class="info-text share-link">{{onewinner_address|addreplaces}}</span>
                    <input type="text" :value="onewinner_address" readonly="readonly" id="share-link1">
                    <a href="" class="info-btn" target="_blank">{{$t('m.winnerIndex.view_source')}}</a>&nbsp;&nbsp;
                    <span class="info-btn" id="j-copy" @click="copy_fun('address')">{{$t('m.winnerIndex.copy')}}</span>
                </div>
            </div>
        </div>
        <div class="winner-con">

            <!-- 第一个 -->
        <div class="winner-box winner-boxOne">
            <div class="winner-end">
                <img class="over-img" src="@/assets/images/winner_end.png" alt="" v-if="!winner_type||openprize">
                <div class="winner-over">
                    <div class="end-head flex-box">
                        <div class="end-head-img">
                            <img src="@/assets/images/winner_img1.png" alt="">
                        </div>
                        <div class="end-head-title">{{$t('m.winnerIndex.title')}}</div>
                    </div>
                    <div class="win-people" v-if="!winner_type">
                        <div class="win-peo-li flex-box">
                            <div class="people-info">
                                <div class="peopel-title">{{$t('m.winnerIndex.winners')}}</div>
                                <div class="people-num">{{winner_address|addreplace(8,8)}}</div>
                            </div>
                            <div class="bonus-box">
                                <div class="bonus-title">{{$t('m.winnerIndex.Indexendtime')}}</div>
                                <div class="people-num" >{{end_time|fullTime}}</div>
                            </div>
                        </div>
                    </div>
                    <div class="winner-timer" v-if="winner_type">
                        <div class="timer-title" v-if="!openprize">{{$t('m.winnerIndex.game_timer')}}</div>
                        <div class="timer-title" v-if="openprize">{{$t('m.winnerIndex.calc_result')}}<!-- {{$t('m.winnerIndex.game_timer')}} --></div>
                        <div class="timer-box flex-box">
                            <div class="timer-list day">
                                <div class="timer-num">{{day}}</div>
                                <div class="unit">{{$t('m.winnerIndex.day')}}</div>
                            </div>
                            <div class="time-icon">:</div>
                            <div class="timer-list hour">
                                <div class="timer-num">{{hour}}</div>
                                <div class="unit">{{$t('m.winnerIndex.hour')}}</div>
                            </div>
                            <div class="time-icon">:</div>
                            <div class="timer-list minute">
                                <div class="timer-num">{{minute}}</div>
                                <div class="unit">{{$t('m.winnerIndex.minute')}}</div>
                            </div>
                            <div class="time-icon">:</div>
                            <div class="second timer-list">
                                <div class="time-num">{{second}}</div>
                                <div class="unit">{{$t('m.winnerIndex.second')}}</div>
                            </div>
                        </div>
                        <div class="timer-decs">{{$t('m.winnerIndex.game_timer_decs')}}</div>
                    </div>
                </div>
                
                <div class="winner-new">
                    <div class="win-rule" :class="{'active':is_rule}">
                        <div class="iconfont" @click="rule_btn()">&#xe719;</div>
                        <div class="rule-con">
                            <ul>
                                <li>{{$t('m.winnerIndex.rule_desc1')}}</li>
                                <li>{{$t('m.winnerIndex.rule_desc2')}}</li>
                                <li>{{$t('m.winnerIndex.rule_desc3')}}</li>
                                <li>{{$t('m.winnerIndex.rule_desc4')}}</li>
                                <li>{{$t('m.winnerIndex.rule_desc5')}}</li>
                                <li>{{$t('m.winnerIndex.rule_desc6')}}</li>
                            </ul>
                        </div>
                    </div>
                    <div class="winner-new-title flex-box">
                        <div class="new-title flex-1">{{$t('m.winnerIndex.winner_new_title')}}</div>
                        <div class="winner-decs" @click="rule_btn()">{{$t('m.winnerIndex.winner_new_decs')}}
                            <div class="winner-icon">
                                <div class="iconfont">&#xe635;</div>
                            </div>
                        </div>
                    </div>
                    <div class="betting-box flex-box">
                        <div class="betting-btn" @click="betting_btn(1)">1</div>
                        <div class="betting-btn" @click="betting_btn(5)">5</div>
                        <div class="betting-btn" @click="betting_btn(10)">10</div>
                        <div class="betting-btn" @click="betting_btn(20)">20</div>
                        <div class="betting-btn" @click="betting_btn(100)">100</div>
                    </div>
                    <div class="betting-num-box flex-box">
                        <div class="betting-num-title">{{$t('m.winnerIndex.betting_num')}}</div>
                        <div class="betting-input flex-1">
                            <input type="text" v-model="bet_count">
                        </div>
                        <div class="ratio-box">{{bet_count}} X {{bet_price}}ETH = {{bonus}}ETH</div>
                    </div>
                    <div class="winner-new-btnBox flex-box">
                        <div class="btnBox-btn flex-1"  @click="send_bet(true)">{{$t('m.winnerIndex.betting_btn1')}}</div>
                        <div class="btnBox-btn vault-btn flex-1"  @click="send_bet(false)">{{$t('m.winnerIndex.betting_btn2')}}</div>
                    </div>
                </div>
            </div>
            <div class="winner-register" v-if="!register">
                <p class="register-txt">{{$t('m.winnerIndex.register_decs')}}0.1ETH</p>
                <div class="register-btn" @click="register_pop()">{{$t('m.winnerIndex.register')}}</div>
            </div>
            <div class="winner-invite" v-if="register">
                <div class="invite-title">{{$t('m.winnerIndex.invite_tit')}}</div>
                <div class="invite-inputBox flex-box">
                    <div class="invite-input flex-1 share-link">
                        <input type="text" :value="invite_link+'/'+user_name" readonly="readonly"  id="share-link">
                    </div>
                    <div class="invite-btn" id="j-copy" @click="copy_fun('invite')"><i class="iconfont">&#xe620;</i></div>
                </div>
                <div class="invite-decs">{{$t('m.winnerIndex.invite_decs')}} 5%</div>
            </div>
        </div>
        <!-- 第一个 -->



            <!-- 第二个 -->
            <div class="winner-box winner-int">
                <div class="int-head">
                    <div class="int-head-con">
                        <div class="int-head-title">{{$t('m.winnerIndex.win_the_prize')}}</div>
                        <div class="int-head-numBox flex-box">
                            <div class="iconfont">&#xe64a;</div>
                            <div class="int-had-num">{{winner_reward}}</div>
                        </div>
                        <div class="dividend flex-box"><div>{{$t('m.winnerIndex.recommend_dividend')}} <span>{{winner_inviter_reward}} <span style="font-size: .6rem;">ETH</span></span></div></div>
                        <!--<div class="int-head-decs">1% {{$t('m.winnerIndex.win_prize_decs1')}}，0.001{{$t('m.winnerIndex.win_prize_decs2')}}</div>-->
                    </div>
                    <div class="int-head-bottom flex-box"><div>{{$t('m.winnerIndex.award_notice1')}}{{last_winner_reward}}ETH，{{$t('m.winnerIndex.award_notice2')}} <span>{{last_winner_address|addreplace(8,5)}}</span></div></div>
                </div>
                <div class="int-info flex-box">
                    <div class="int-list flex-1">
                        <div class="int-list-title">{{$t('m.winnerIndex.int_list_title1')}}</div>
                        <div class="int-list-num" v-if="winner_type">{{my_win_rate}}%</div>
                        <div class="int-list-num" v-if="!winner_type"> - </div>
                        <div class="int-list-decs"></div>
                        <div class="int-bettingBox flex-box">
                            <div class="int-betting flex-1">
                                <div class="int-betting-title">{{$t('m.winnerIndex.my_betting')}}</div>
                                <div class="int-betting-num" v-if="winner_type">{{my_bet_count}}</div>
                                <div class="int-betting-num" v-if="!winner_type"> - </div>
                            </div>
                            <div class="int-betting flex-1">
                                <div class="int-betting-title">{{$t('m.winnerIndex.all_betting')}}</div>
                                <div class="int-betting-num" v-if="winner_type">{{total_bet_count}}</div>
                                <div class="int-betting-num" v-if="!winner_type"> - </div>
                            </div>
                        </div>
                    </div>
                    <div class="int-list flex-1">
                        <div class="int-list-title">{{$t('m.winnerIndex.int_list_title2')}}</div>
                        <div class="int-list-num" style="font-size:1.2rem;">{{airdrop}} <span style="font-size: .6rem;">ETH</span></div>
                        <div class="int-list-decs">{{$t('m.winnerIndex.int_list_decs')|langfmt("0.5eth")|langfmt("1‰")}}</div>
                        <div class="int-list-luck">{{$t('m.winnerIndex.int_preceding0')}} {{luck_num}}</div>
                        <div class="int-preceding">{{$t('m.winnerIndex.int_preceding1')}}{{last_airdrop}}ETH，{{$t('m.winnerIndex.int_preceding2')}} <span> <br />{{last_winner_address|addreplace(8,5)}}</span></div>
                    </div>
                </div>
            </div>
            <!-- 第二个 -->




            <!-- 第三个 -->
            <div class="winner-box winner-playedVault">
                <div class="playedVault-tab flex-box">
                    <div class="playedVault-item" :class="{'is-active':activeName=='played'}" @click="handleClick('played')"><div class="pv-tab-txt">{{$t('m.winnerIndex.playedVault_tit1')}}</div></div>
                    <div class="playedVault-item" :class="{'is-active':activeName=='vault'}" @click="handleClick('vault')"><div class="pv-tab-txt">{{$t('m.winnerIndex.playedVault_tit2')}}</div></div>
                </div>
                <div class="played-table" v-if="activeName=='played'">
                    <div class="tab-head tab-bg flex-box">
                        <div class="tab-head-list tab-name">{{$t('m.winnerIndex.player')}}</div>
                        <div class="tab-head-list tab-betting">{{$t('m.winnerIndex.betting')}}</div>
                        <div class="tab-head-list tab-luck">{{$t('m.winnerIndex.luck_num')}}</div>
                        <div class="tab-head-list text-right flex-1">{{$t('m.winnerIndex.transaction_time')}}</div>
                    </div>
                    <div class="tab-con">
                        <div class="tab-con-list flex-box" v-for="(tx,index)  in tx_list" :key="index" :class="{'tab-bg':index%2 != 0}">
                            <div class="tab-item tab-name">{{tx.address|addreplace(8,5)}}  </div>
                            <div class="tab-item tab-betting">{{tx.bet_count}} </div>
                            <div class="tab-item tab-luck" v-if="tx.luck_num>0" v-bind:style="tx.win?'color:#ff7a3f;':''"> {{tx.luck_num}} <span v-if="tx.win" class="iconfont">&#xe615;</span></div>
                            <div class="tab-item tab-luck" v-if="tx.luck_num==0"> - </div>
                            <div class="tab-item text-right flex-1" style="font-size: .4rem;">{{tx.bet_time}}</div>
                        </div>
                    </div>
                </div>
                <div class="vault-box" v-if="activeName=='vault'">
                    <div class="vault-us">
                        <div class="us-title">{{$t('m.winnerIndex.playedVault_tit2')}}</div>
                        <div class="vault-num">{{my_vault}}<span>ETH</span></div>
                        <div class="p-vault-btn" v-on:click="send_withdraw()">{{$t('m.winnerIndex.raise_btn')}}</div>
                        <div class="vault-decs">{{$t('m.winnerIndex.vault_decs')}}</div>
                    </div>
                    <div class="vault-block flex-box">
                        <div class="vault-item">
                            <div class="vault-item-title">{{$t('m.winnerIndex.win_treasure')}}</div>
                            <div class="vault-item-num">{{my_jackpot_stat}} <span>ETH</span> </div>
                        </div>
                        <div class="vault-item">
                            <div class="vault-item-title">{{$t('m.winnerIndex.airdrop')}}</div>
                            <div class="vault-item-num">{{my_airdrop_stat}}  <span>ETH</span></div>
                        </div>
                        <div class="vault-item">
                            <div class="vault-item-title">{{$t('m.winnerIndex.invite_reward')}}</div>
                            <div class="vault-item-num">{{my_invite_reward_stat}}  <span>ETH</span></div>
                        </div>
                        <div class="vault-item">
                            <div class="vault-item-title">{{$t('m.winnerIndex.invite_win_treasure')}}</div>
                            <div class="vault-item-num">{{my_invite_jackpot_reward_stat}}  <span>ETH</span></div>
                        </div>
                        <div class="vault-item">
                            <div class="vault-item-title">{{$t('m.winnerIndex.invite_airdrop')}}</div>
                            <div class="vault-item-num">{{my_invate_airdrop_reward_stat}}  <span>ETH</span></div>
                        </div>
                        <div class="vault-item">
                            <div class="vault-item-title">{{$t('m.winnerIndex.all_airdrop')}}</div>
                            <div class="vault-item-num">{{my_ess_airdrop_stat}}  <span>ESS</span></div>
                        </div>
                    </div>
                </div>
            </div>
            <!-- 第三个 -->
        </div>
    </div>
        <div class="footer">
            <div class="line2">{{$t('m.public.footer.tip')}}</div>
           <!--  <div class="line2">EOS Mainnet via Greymass and EOS Asia</div> -->
        </div>
    </div>
    
</div>
    <div class="register-pop" :class="{'active':is_register}">
        <div class="pop-con">
            <div class="close-btn" @click="close_pop()">
                <div class="iconfont">&#xe719;</div>
            </div>
            <div class="register-pop-title">{{$t('m.winnerIndex.register')}}</div>
            <div class="input-decs">{{$t('m.winnerIndex.input_decs')}}</div>
            <div class="register-pop-con flex-box">
                <div class="register-pop-input">
                    <input type="text" v-model="register_name"  :placeholder="$t('m.winnerIndex.register_place')" maxlength='10'>
                </div>
                <div class="register-pop-btn" @click="send_register()">{{$t('m.winnerIndex.register')}}</div>
            </div>
            <div class="register-pop-decs">{{$t('m.winnerIndex.register_decs')}}0.1ETH</div>
        </div>
        <div class="pop-mask"></div>
    </div>
    <div class="go-top" @click="go_top()" v-if="is_goTop"><div class="iconfont">&#xe60f;</div></div>
    <el-dialog
    :title="$t('m.winnerIndex.betting_success')"
    :visible.sync="dialogVisible"
    width="30%"
    :before-close="handleClose" class="betting-success">
    <div class="dialog-con">
        <p class="dialog-con-order">{{$t('m.winnerIndex.txhash')}}：<span>{{txhash_val}}</span></p>
    </div>
    <span slot="footer" class="dialog-footer">
        <a href="javascript(0)" target="_Blank" class="dialog-con-btn look-btn">{{$t('m.winnerIndex.view_more')}}</a>
        <el-button class="dialog-con-btn" type="primary" @click="dialogVisible = false">{{$t('m.public.confirm')}}</el-button>
    </span>
    </el-dialog>
</div>

</template>
<script>
import router from "@/router";
export default {
    components: {
    },
    data () {
        return {
            decimals:4, //显示的精度

            onewinner_address:onewinnerAddress,
            account_address:"",  //当前账户地址
            round:0,
            end_time:0,//本轮结束时间
            less_time:0,
            less_time_show:"",
            bet_price:0,
            bet_count:0,
            airdrop:0,
            luck_num:0,
            last_airdrop:0,
            last_airdrop_address:"",
            winner_address:"",
            winner_reward:0,
            winner_inviter_reward:0,
            last_winner_address:"",
            last_winner_reward:0,
            total_bet_count:0,
            my_bet_count:0,
            my_win_rate:0,
            user_name:"",
            register_name:"",
            my_vault:0,
            my_jackpot_stat:0,
            my_airdrop_stat:0,
            my_invite_reward_stat:0,
            my_invite_jackpot_reward_stat:0,
            my_invate_airdrop_reward_stat:0,
            //交易列表
            tx_list:[],
            //定时器
            timer:null,
            winner_start_time:null,
            winner_end_time:null,

            day:'00',
            hour:'00',
            minute:'00',
            second:'00',
            bet_count:1,//投注数量
            ratio:0.1,//投注奖金比例
            register:true,//是否注册，false未注册，true已注册
            invite_link:menu_config.onewinner,
            winner_type:false,//夺宝是否结束，false结束，true开始
            activeName:'played',
            is_register:false,//注册弹窗，false关闭，true打开
            is_rule:false,//投注规则，false关闭，true打开
            openprize:true,//false不是开奖中，true开奖中,
            is_goTop:false,//返回头部按钮，false隐藏，true显示
            test:'',
            dialogVisible: false,
            txhash_val:''
        }
    },
     created(){
          if(!checkweb3()){
              return false;
          }

          var _vue = this;
          //任务的队列，txrecipet=>funcname表示，轮询到对应的tx时，执行合约的方法
          window.eventQueue = {};
          //轮询区块信息，回调事件
          window.loopBlock = function(lastblock){
              web3.eth.getBlockNumber().then(function(newblock){
                // console.log(newblock);
                  //只处理新块
                  if(newblock>lastblock){
                      _vue.init_round();
                      _vue.load_account();
                      _vue.load_vault();
                      _vue.load_txlist();
                      web3.eth.getBlock(newblock).then(function(blockinfo){
                          console.log(blockinfo);
                          console.log(eventQueue);
                          try{
                              for(var i=0;i<blockinfo.transactions.length;i++){
                                  var txhash = blockinfo.transactions[i];
                                  for(var queuehash in eventQueue){
                                      if(txhash==queuehash){
                                          var funcname = eventQueue[queuehash];
                                          delete eventQueue[queuehash];
                                          try{
                                              eval(funcname+"('"+txhash+"')");
                                          }catch(ex){
                                              console.log(ex);
                                              console.log(funcname+"不存在");
                                              if (funcname=="bet_done") {
                                                // 下注成功
                                                    _vue.$message({
                                                      message: "下注成功",
                                                      type: 'success'
                                                    });  
                                              }
                                          }
                                      }
                                  }
                              }
                          }catch (ex) {
                          }
                          loopBlock(newblock);
                      });
                  }
                  else
                  {
                      loopBlock(newblock);
                  }
              });
          };
          loopBlock(0);
          //end loop

        _vue.account_address = window.eth_account;
        _vue.init_round();
        _vue.load_account();
        _vue.load_vault();
        _vue.load_txlist();
    },
    watch:{
        register_name:function(){
            
            var regex=/^\w+$/;
            if(this.register_name==''||this.register_name==null){
                this.test = this.register_name;
            }else{
                if(!(regex.test(this.register_name))){
                    this.register_name = this.test;
                }else{
                    this.test = this.register_name;
                }
            }
        },
        account_address:function(){
            // 切换账号后需要刷新的数据
            var _vue=this;
            this.$message({
            message: _vue.$t('m.winnerIndex.account_address')+this.account_address,
            type: 'success'
            });  
              _vue.init_round();
              _vue.load_account();
              _vue.load_vault();
              _vue.load_txlist();
        },
    },
    mounted () {   
        //监听滚动，判断是否显示返回顶部按钮
        var box = this.$refs.viewBox;
        box.addEventListener('scroll',() => {
            if(this.$refs.viewBox.scrollTop>100){
                this.is_goTop=true;
            }else{
                this.is_goTop = false;
            }
          }, false)
    },
    computed:{
        //投注获得ETH算法
        bonus: function () {
            
            if(this.bet_count==''||this.bet_count==0){
                this.bet_count=0;
            }else if(this.bet_count>100){
                this.bet_count=100;
            }else{
                this.bet_count = parseFloat(this.bet_count);
            }
            return (this.bet_count*1000000 * this.bet_price*1000000)/1000000000000
        },
    },
    methods:{
        /**
         * 选择投注数量
         */
        betting_btn(val){
            this.bet_count = val;
        },
        /**
         * 切换参与/金库
         */
        handleClick(tab) {
            this.activeName = tab;
        },
        /**
         * 注册弹窗
         */
        register_pop(){
            this.is_register=true;
        },
        close_pop(){
            this.is_register=false;
        },
        /**
         * 投注规则
         */
        rule_btn(){
            if(this.is_rule){
                this.is_rule = false;
            }else{
                this.is_rule = true;
            }
        },
        /**
         * 复制
         */
        copy_fun(link){
            const _vue = this;
            if(link=='invite'){
                $("#share-link").select();
            }else{
                $("#share-link1").select();
            }
            document.execCommand("copy",false,null);
            _vue.$message({
                message: _vue.$t('m.winnerIndex.copyed'),
                center: true
            });
        },
        /**
         * 返回顶部
         */
        go_top(){
            $('.content').animate({scrollTop:0}, 500);
        },
        /**
         * 下注成功弹窗
         */
        handleClose(done) {
            this.dialogVisible=false;
        },
        link_detail(){
           location.reload();
        },
        /**
         * 字符串截断
         */
        addressValue(str,start,length,replace){
            try {
                var firstStr = str.substr(0, start);
                var endStr = str.substr(start + length, str.length);
                return firstStr + replace + endStr;
            }catch(ex){

            }
        },
       send_withdraw(){
           if(!checkweb3()){
               return false;
           }
           var price = 0;
           var recommend_gas_price = "20";
           var _vue = this;
           onewinnerContract.methods.withdraw().send({from: _vue.account_address,value:price,gasPrice:web3.utils.toWei(recommend_gas_price,"gwei")},function(err,txreceipt){
               if(!err){
                   eventQueue[txreceipt] = "withdraw_done";
                   window.withdraw_done = function(txhash){
                        _vue.$message({
                          message: _vue.$t('m.winnerIndex.withdraw'),
                          type: 'success'
                        });  
                   };
               }
           });
       },
       send_register(){
           if(!checkweb3()){
               return false;
           }
           var _vue = this;

           onewinnerContract.methods.getAddressByUserName(_vue.register_name).call(null,function(err,value){
               if(!err)
               {
                   var registed_name = value.valueOf();
                   if(registed_name=="0x0000000000000000000000000000000000000000"){
                       var price = web3.utils.toWei("0.1","ether");
                       var recommend_gas_price = "20";
                       var invite_id = localStorage.getItem("onewinner_invite")&&!isNaN(localStorage.getItem("onewinner_invite"))?parseInt(localStorage.getItem("onewinner_invite")):0;
                       onewinnerContract.methods.register(_vue.register_name,invite_id).send({from: _vue.account_address,value:price,gasPrice:web3.utils.toWei(recommend_gas_price,"gwei")},function(err,txreceipt){
                           if(!err){
                               eventQueue[txreceipt] = "register_done";
                               window.register_done = function(txhash){
                                   _vue.$message({
                                       message: _vue.$t('m.winnerIndex.succ_regis'),
                                       type: 'success'
                                   });
                                   _vue.is_register=false;
                               };
                           }
                       });
                   }
                   else {
                       _vue.$message({
                           message: _vue.$t('m.winnerIndex.name_exist'),
                           type: 'error'
                       });
                   }
               }
           });
       },
      send_bet(usewallet){
          if(!checkweb3()){
              return false;
          }
           var _vue = this;
           var price = web3.utils.toWei((_vue.bet_count * _vue.bet_price).toString(),"ether");
           if(!usewallet){
               price = 0;
           }
           var invite_id = localStorage.getItem("onewinner_invite")&&!isNaN(localStorage.getItem("onewinner_invite"))?parseInt(localStorage.getItem("onewinner_invite")):0;
           var recommend_gas_price = "20";
          onewinnerContract.methods.bet(_vue.bet_count,invite_id).send({from: _vue.account_address,value:price,gasPrice:web3.utils.toWei(recommend_gas_price,"gwei")},function(err,txreceipt){
               if(!err){
                   eventQueue[txreceipt] = "bet_done";
                   window.bet_done = function(txhash){
                       web3.eth.getTransactionReceipt(txhash,function(err,txreceipt){
                           console.log(txreceipt);
                           if(txreceipt.logs.length==2)
                           {
                                _vue.$message({
                                  message: _vue.$t('m.winnerIndex.you_lose_airdrop').replace("%s",web3.utils.hexToNumber(txreceipt.logs[0].data)),
                                  type: 'success'
                                });
                           }
                           else
                               if(txreceipt.logs.length==3){

                                _vue.$message({
                                  message: _vue.$t('m.winnerIndex.you_win_airdrop').replace("%s",web3.utils.hexToNumber(txreceipt.logs[0].data)),
                                    type: 'success'
                                });
                           }
                       });

                        // _vue.$message({
                        //   message: _vue.$t('m.winnerIndex.betting_success')+txhash,
                        //   type: 'success'
                        // });  
                        _vue.txhash_val = txhash;
                        _vue.dialogVisible = true;
                   };
               }
           });
     },

     //刷新token账户信息
     init_round(){
         var _vue = this;
         var current_link = location.href;
         var site_link = _vue.invite_link;
         var regexp = new RegExp(site_link+"/([^#]+)#/");
         var regexp_result = regexp.exec(current_link);
         if(regexp_result){
             //加载邀请人
             var inviter_name = regexp_result[1];  //名字将从路由中获得
             onewinnerContract.methods.getAddressByUserName(inviter_name).call(null,function(err,value){
                 if(!err)
                 {
                     var inviter_address = value.valueOf();
                     onewinnerContract.methods.getUserIdByAddress(inviter_address).call(null,function(err,value){
                         if(!err)
                         {
                             var inviter_id = value.valueOf();
                             if(inviter_id>0){
                                 localStorage.setItem("onewinner_invite",inviter_id);
                             }
                         }
                     });
                 }
             });
         }

         //加载第x轮
         onewinnerContract.methods.round().call(null,function(err,value){
             if(!err)
             {
                 _vue.round = value.valueOf();
                 _vue.load_round(_vue.round);
             }
         });

         onewinnerContract.methods.betPrice().call(null,function(err,value){
             if(!err)
             {
                 _vue.bet_price = web3.utils.fromWei(value.valueOf(),"ether");
             }
         });


     },
     load_round(round_index){
         //倒计时
         var _vue = this;
         onewinnerContract.methods.getEndTime(round_index).call(null,function(err,value){
             if(!err){
                 _vue.end_time = value.valueOf();
                 if(_vue.end_time>0){
                     window.nowTime = Math.floor(new Date().getTime()/1000);
                     _vue.less_time = _vue.end_time - nowTime;
                     if(!_vue.timer&&_vue.less_time>0){
                         _vue.timer = setInterval(function(){
                             _vue.less_time--;
                             var less_time = _vue.less_time;
                             var day = Math.floor(less_time/(3600*24));
                             less_time = less_time%(3600*24);
                             var hour = Math.floor(less_time/3600);
                             less_time = less_time%3600;
                             var minute = Math.floor(less_time/60);
                             var second = less_time%60;
                             _vue.less_time_show = day+"天"+hour+"小时"+minute+"分钟"+second+"秒";
                             if(_vue.less_time<=0){
                                 clearInterval(_vue.timer);
                             }
                             if (less_time>0) {

                               _vue.winner_type = true; //还在进行中
                               _vue.openprize = false;  //未开奖

                               if (day<10) {
                               _vue.day="0"+day;
                               }else{
                               _vue.day=day;
                               }
                               if (hour<10) {
                               _vue.hour="0"+hour;
                               }else{
                               _vue.hour=hour;
                               }
                               if (minute<10) {
                               _vue.minute="0"+minute;
                               }else{
                               _vue.minute=minute;
                               }
                               if (second<10) {
                               _vue.second="0"+second;
                               }else{
                               _vue.second=second;
                               }
                             }else{
                               _vue.winner_type = true; //还在进行中
                               _vue.openprize = true;  //开奖中
                               _vue.day="00";
                               _vue.hour="00";
                               _vue.minute="00";
                               _vue.second="00";
                             }
                         },1000);
                     }
                 }
             }
         });
         //上期中奖奖金
         if(round_index>1)
         onewinnerContract.methods.getReward(round_index-1).call(null,function(err,value){
             if(!err)
             {
                 _vue.last_winner_reward = Number(web3.utils.fromWei(value.valueOf(),"ether")).toFixed(_vue.decimals);
             }
         });
         //上期中奖人
         if(round_index>1)
         onewinnerContract.methods.winner(round_index-1).call(null,function(err,value){
             if(!err)
             {
                 _vue.last_winner_address = value.valueOf();
             }
         });
         //夺宝奖池
         onewinnerContract.methods.getReward(round_index).call(null,function(err,value){
             if(!err)
             {
                 _vue.winner_reward = Number(web3.utils.fromWei(value.valueOf(),"ether")).toFixed(_vue.decimals);
                 _vue.winner_inviter_reward =   Number(_vue.winner_reward * 0.2 * 0.25).toFixed(_vue.decimals);
             }
         });
         //中奖人
         onewinnerContract.methods.isRun(round_index).call(null,function(err,value){
             if(!err){
                 if(!value.valueOf())
                 {
                     onewinnerContract.methods.winner(round_index).call(null,function(err,value){
                         if(!err)
                         {
                             _vue.winner_address =  value.valueOf();
                             _vue.winner_type = false;   //不在进行中
                             _vue.openprize = false;  //不在开奖中，表示已开奖
                         }
                     });
                 }
                 else {
                     _vue.winner_type = true;  //正在进行中
                     _vue.openprize = false;   //不在开奖中，表示未开奖
                 }
             }
         });

         //上次的空投
         onewinnerContract.methods.airdropWinner().call(null,function(err,value){
             if(!err)
             {
                 if(value.valueOf()!=0x0)
                 _vue.last_airdrop_address =  value.valueOf();
             }
         });
         onewinnerContract.methods.airdropWinnerReward().call(null,function(err,value){
             if(!err)
             {
                 _vue.last_airdrop =  Number(web3.utils.fromWei(value.valueOf(),"ether")).toFixed(_vue.decimals);
             }
         });
         //本次空投
         onewinnerContract.methods.airdrop().call(null,function(err,value){
             if(!err)
             {
                 _vue.airdrop =  Number(web3.utils.fromWei(value.valueOf(),"ether")).toFixed(_vue.decimals);
             }
         });
         //空投幸运号
         onewinnerContract.methods.luckNum().call(null,function(err,value){
             if(!err)
             {
                 _vue.luck_num =  value.valueOf();
             }
         });
         //我的投注
         onewinnerContract.methods.getBetCount(round_index).call(null,function(err,value){
             if(!err)
             {
                 _vue.total_bet_count =  value.valueOf();
                 onewinnerContract.methods.getMyBetCount(round_index,_vue.account_address).call(null,function(err,value){
                     if(!err)
                     {
                         _vue.my_bet_count =  value.valueOf();
                         _vue.my_win_rate = Math.round(_vue.my_bet_count * 100 / _vue.total_bet_count);
                     }
                 });
             }
         });

     },
     //加载账户信息
     load_account(){
         var _vue = this;
         //用户名
         onewinnerContract.methods.getUserNameByAddress(_vue.account_address).call(null,function(err,value){
             if(!err){
                 _vue.user_name = value.valueOf();
                 if (_vue.user_name=='' ) {
                    _vue.register=false;
                 }else{
                    _vue.register=true;

                 }
             }
         });
        web3.eth.getAccounts(function(err,accounts) {
                if (err != null || accounts.length == 0) {
                    //console.log("小狐狸还没有登录，锁住中");
                    MessageBox.confirm(i18n.t("m.public.address_not_found"), i18n.t("m.public.tip"), {
                          confirmButtonText: i18n.t("m.public.confirm"),
                          showClose:false,
                          showCancelButton:false,
                          type: 'warning'
                    }).then(() => {
                        // 确定
                      location.reload();
                    }).catch(() => {    
                      location.reload();   
                    });
                }
                else {
                    _vue.account_address=accounts[0];
                }
            });
     },
     //加载交易列表
     load_txlist(){
          var _vue = this;
         onewinnerContract.methods.getTxList(50).call(null,function(err,value){
             if(!err){
                 _vue.tx_list = [];
                 var _tx_list_data = value.valueOf();
                 for(var i=0;i<_tx_list_data[0].length;i++){
                     var _address = _tx_list_data[0][i];

                     if(_address=="0x0000000000000000000000000000000000000000"){
                         break;
                     }
                     var _betCount = _tx_list_data[1][i];
                     var _timeStamp = _tx_list_data[2][i];
                     var _betTime = new Date(_timeStamp*1000).format("MM-dd hh:mm:ss");
                     var _luckNum = _tx_list_data[3][i];
                     var _win = _tx_list_data[4][i];
                     _vue.tx_list[i] = {"address":_address,"bet_count":_betCount,"bet_time":_betTime,"luck_num":_luckNum,"win":_win};
                 }
             }
         });
     },
     //加载我的金库
     load_vault(){
           var _vue = this;
           //我的金库
         onewinnerContract.methods.myVault(_vue.account_address).call(null,function(err,value){
             if(!err){
                 _vue.my_vault = Number(web3.utils.fromWei(value.valueOf(),"ether")).toFixed(_vue.decimals);
             }
             else
                 console.log(err);

         });
         //累计夺宝中奖
         onewinnerContract.methods.getStatJackpotReward(_vue.account_address).call(null,function(err,value){
             if(!err){
                 _vue.my_jackpot_stat =  Number(web3.utils.fromWei(value.valueOf(),"ether")).toFixed(_vue.decimals);
             }
         });
         //累计空投中奖
         onewinnerContract.methods.getStatAirdropReward(_vue.account_address).call(null,function(err,value){
             if(!err){
                 _vue.my_airdrop_stat =  Number(web3.utils.fromWei(value.valueOf(),"ether")).toFixed(_vue.decimals);
             }
         });
         //返利
         onewinnerContract.methods.getStatInviterReward(_vue.account_address).call(null,function(err,value){
             if(!err){
                 _vue.my_invite_reward_stat =  Number(web3.utils.fromWei(value.valueOf(),"ether")).toFixed(_vue.decimals);
             }
         });
         //夺宝分红
         onewinnerContract.methods.getStatInviterJackpotReward(_vue.account_address).call(null,function(err,value){
             if(!err){
                 _vue.my_invite_jackpot_reward_stat =  Number(web3.utils.fromWei(value.valueOf(),"ether")).toFixed(_vue.decimals);
             }
         });
         //空投分红
         onewinnerContract.methods.getStatInviterAirdropReward(_vue.account_address).call(null,function(err,value){
             if(!err){
                 _vue.my_invate_airdrop_reward_stat =  Number(web3.utils.fromWei(value.valueOf(),"ether")).toFixed(_vue.decimals);
             }
         });
         //ess空投
         onewinnerContract.methods.getStatEssAirdropReward(_vue.account_address).call(null,function(err,value){
             if(!err){
                 _vue.my_ess_airdrop_stat =  Number(web3.utils.fromWei(value.valueOf(),"ether")).toFixed(_vue.decimals);
             }
         });

     }
    }
}
</script>
