                    bottom: 100%;
                    transition: all 0.3s;
                    z-index: 2;
                    overflow: hidden;
                    pointer-events: none;
                    .iconfont{
                        position: absolute;
                        right:0.5rem;
                        top: 0.5rem;
                        font-size: 0.8rem;
                        color: #888fa7;
                    }
                }
                .win-rule.active{
                    bottom: 0;
                    pointer-events: auto;
                }
                .winner-new-title {
                    line-height: 2rem;
                    .new-title {
                        font-size: 0.8rem;
                    }
                    .winner-decs {
                        font-size: 0.7rem;
                        color: #888fa7;
                        position: relative;
                        padding-right: 1rem;
                    }
                    .winner-icon {
                        position: absolute;
                        right: 0;
                        top: 50%;
                        transform: translateY(-50%);
                        .iconfont{
                            font-size: 0.7rem;
                            cursor: pointer;
                            -webkit-tap-highlight-color: rgba(0,0,0,0);
                            
                        }
                        
                    }
                }
                .betting-box {
                    margin: 0 -0.3rem;
                    .betting-btn {
                        width: 17.625%;
                        margin: 0 0.3rem;
                        text-align: center;
                        font-size: 1rem;
                        line-height: 2rem;
                        height: 2rem;
                        background: #7481b8;
                        color: #fff;
                        border-radius: 4px;
                        cursor: pointer;
                        -webkit-tap-highlight-color: rgba(0,0,0,0);
                        
                        &:hover{
                            background: #5b6696;
                        }
                    }
                }
                .betting-num-box {
                    background: #eef0f7;
                    border-radius: 4px;
                    padding: 0.5rem;
                    margin-top: 0.6rem;
                    .betting-num-title {
                        font-size: 0.7rem;
                        line-height: 0.9rem;
                        color: #888fa7;
                        text-align: center;
                        margin-right: 1rem;
                        width: 1.5rem;
                    }
                    .betting-input {
                        height: 1.8rem;
                        line-height: 1.8rem;
                        
                        input{
                            border: none;
                            background: none;
                            display: block;
                            height: 100%;
                            width: 100%;
                            font-size: 1.5rem;
                            &::-webkit-input-placeholder{
                                font-size: 1rem;
                                color: #888fa7;
                            }
                        }
                    }
                    .ratio-box {
                        font-size: 0.8rem;
                        color: #888fa7;
                    }
                }
                .winner-new-btnBox {
                    margin-top: 1.1rem;
                    .btnBox-btn {
                        cursor: pointer;
                        
                        
                        height: 2.7rem;
                        line-height: 2.7rem;
                        text-align: center;
                        border-radius: 4px;
                        background: #ef1e58;
                        color: #fff;
                        font-size: 1rem;
                        &:hover{
                            background: #e4245b;
                        }
                    }
                    .btnBox-btn.vault-btn {
                        background: #7dafff;
                        margin-left: 0.8rem;
                        &:hover{
                            background: #74a8fb;
                        }
                    }
                }
                
            }
            .winner-timer {
                background: #fff;
                text-align: center;
                margin: 1.4rem 0 1.8rem;
                .timer-title {
                    font-size: 0.8rem;
                    line-height: 2rem;
                }
                .timer-box {
                    justify-content: center;
                    align-items: start;
                    font-size: 2.3rem;
                    margin: 0.5rem 0;
                    .timer-list {
                        text-align: center;
                        line-height: 2rem;
                    }
                    .time-icon{
                        line-height: 1.8rem;
                        font-size: 2rem;
                    }
                    .unit {
                        font-size: 0.8rem;
                        line-height: 1.2rem;
                        color: #888fa7;
                    }
                }
                .timer-decs{
                    font-size: 0.6rem;
                    line-height: 1rem;
                    color: #888fa7;
                }
            }
            .winner-register {
                background: #232c52;
                color: #888fa7;
                padding: 0.6rem 1.5rem 1.1rem;
                .register-txt {
                    font-size: 0.8rem;
                    text-align: center;
                    line-height: 2rem;
                    font-size: 0.8rem;
                    text-align: center;
                    line-height: 1rem;
                    margin: 0.65rem 0;
                }
                .register-btn {
                    cursor: pointer;
                    -webkit-tap-highlight-color: rgba(0,0,0,0);
                    
                    width: 10.5rem;
                    height: 2.2rem;
                    line-height: 2.2rem;
                    text-align: center;
                    color: #fff;
                    background: #ef1e58;
                    margin: 0 auto;
                    border-radius: 4px;
                    margin-top: 0.3rem;
                    &:hover{
                        background: #e4245b;
                    }
                }
            }
            .winner-invite {
                background: #222b52;
                padding: 0.3rem 1.5rem;
                .invite-title {
                    color: #fff;
                    font-size: 0.8rem;
                    line-height: 1.8rem;
                }
                .invite-input {
                    height: 2rem;
                    line-height: 2rem;
                    background: #fff;
                    border-radius: 4px;
                    padding: 0 0.8rem;
                    input{
                        display: block;
                        border: none;
                        background: none;
                        height: 100%;
                        width: 100%;
                        color: #888fa7;
                        font-size: 0.8rem;
                    }
                }
                .invite-btn {
                    cursor: pointer;
                    
                    
                    width: 4rem;
                    text-align: center;
                    height: 2rem;
                    border-radius: 4px;
                    line-height: 2rem;
                    color: #fff;
                    background: #ef1e58;
                    margin-left: 0.6rem;
                    &:hover{
                        background: #e4245b;
                    }
                    .iconfont{
                        font-size: 0.9rem;
                    }
                }
                .invite-decs {
                    font-size: 0.7rem;
                    color: #888fa7;
                    line-height: 1rem;
                    margin-top: 0.8rem;
                }
            }
            .winner-int {
                padding: 1.5rem 0;
                .int-head {
                    position: relative;
                    padding: 0 1.5rem 0.8rem;
                    text-align: center;
                    &::after {
                        display: block;
                        content: '';
                        position: absolute;
                        bottom: 0;
                        left: 1.5rem;
                        right: 1.5rem;
                        height: 1px;
                        background: #dde0ec;
                    }
                    .int-head-con {
                        padding: 1.8rem 0 3rem;
                    }
                    .int-head-title {
                        font-size: 0.8rem;
                        line-height: 2rem;
                    }
                    .int-head-numBox {
                        font-size: 2.3rem;
                        line-height: 3rem;
                        justify-content: center;
                        .iconfont{
                            font-size: 1.7rem;
                            color: #6f79af;
                            margin-right: 0.5rem;
                        }
                    }
                    .dividend {
                        width: 12.5rem;
                        margin: 0 auto;
                        background: #f4f6fc;
                        min-height: 2.2rem;
                        line-height: 1.2rem;
                        text-align: center;
                        border-radius: 4px;
                        font-size: 0.8rem;
                        color: #f63953;
                        margin-top: 0.5rem;
                        justify-content: center;
                        padding: 0.3rem;
                        span {
                            font-size: 1.1rem;
                        }
                    }
                    .int-head-decs {
                        font-size: 0.7rem;
                        line-height: 1.9rem;
                        text-align: center;
                    }
                    .int-head-bottom {
                        font-size: 0.7rem;
                        text-align: center;
                        background: #f4f6fc;
                        border-radius: 4px;
                        padding: 0.3rem;
                        line-height: 1rem;
                        min-height: 2.2rem;
                        justify-content: center;
                        span{
                            color: #165eee;
                        }
                    }
                }
                .int-info {
                    padding-top: 1.8rem;
                    align-items: start;
                    .int-list {
                        min-height: 13.5rem;
                        text-align: center;
                        padding: 0.9rem 1.5rem 0;
                        width: 50%;
                        flex-shrink: 0;
                        &:last-child{
                            position: relative;
                            &::after{
                                position: absolute;
                                display: block;
                                content: '';
                                width: 1px;
                                left: 0;
                                height: 100%;
                                background: #dde0ec;
                                top: 0;
                            }
                        }
                    }
                    .int-list-title {
                        font-size: 0.8rem;
                        line-height: 1.3rem;
                    }
                    .int-list-num {
                        font-size: 2rem;
                        line-height: 3.1rem;
                        height: 3.1rem;
                        span{
                            font-size: 1rem;
                        }
                    }
                    .int-list-decs {
                        font-size: 0.6rem;
                        line-height: 1rem;
                        text-align: left;
                        min-height: 2.4rem;
                        color: #888fa7;
                        word-break: break-all;
                    }
                    .int-bettingBox {
                        margin-top: 0.9rem;
                    }
                    .int-betting{
                        text-align: left;
                    }
                    .int-betting-title {
                        font-size: 0.8rem;
                        line-height: 1.2rem;
                        min-height: 2.4rem;
                    }
                    .int-betting-num {
                        font-size: 1rem;
                        line-height: 1.7rem;
                        text-align: center;
                        
                    }
                    .int-list-luck {
                        font-size: 0.75rem;
                        line-height: 1rem;
                        color: #64a0ff;
                        margin-top: 0.3rem;
                        margin-bottom: 0.6rem;
                    }
                    .int-preceding {
                        
                        font-size: 0.7rem;
                        background: #f4f6fc;
                        border-radius: 4px;
                        padding: 0.3rem;
                        line-height: 1rem;
                        word-break: break-all;
                        span{
                            color: #165eee;
                        }
                    }
                }
            }
            .winner-playedVault{
                padding: 0.3rem 0 0.9rem;
                min-height: 35.8rem;
                .playedVault-tab{
                    padding: 0 1.5rem;
                    position: relative;
                    .playedVault-item {
                        color: #3e4656;
                        line-height: 2.9rem;
                        height: 2.9rem;
                        font-size: 0.8rem;
                        padding: 0 1.5rem;
                        position: relative;
                        z-index: 9;
                        cursor: pointer;
                        -webkit-tap-highlight-color: rgba(0,0,0,0);
                        
                        &:first-child{
                            padding-left:0; 
                        }
                        &:hover{
                            color: #ee1b59;
                        }
                    }
                    .playedVault-item.is-active{
                        .pv-tab-txt{
                            position: relative;
                            
                            &::after{
                                bottom: 0;
                                position: absolute;
                                display: block;
                                content: '';
                                left: 0;
                                width: 100%;
                                background-color: #ee1b59;
                                height: 2px;
                            }
                        }
                    }
                    &::after{
                        bottom: 0;
                        position: absolute;
                        display: block;
                        content: '';
                        left: 0;
                        height: 2px;
                        width: 100%;
                        background-color: #dde0ec;
                    }
                }
                .tab-con{
                    max-height: 29.9rem;
                    overflow-x: hidden;
                    overflow-y: scroll;
                    &::-webkit-scrollbar{
                        position: absolute;
                        width: 0.2rem;

                    }
                }
                .tab-head {
                    height: 1.8rem;
                    padding: 0 1.5rem;
                    font-size: 0.7rem;
                    color: #888fa7;
                }
                .tab-item,.tab-head-list{
                    flex: 1;
                }
                .tab-betting {
                    width: 14%;
                    text-align: center;
                    flex-shrink: 0;
                    flex: initial;
                }
                
                .text-right {
                    text-align: right;
                }
                .tab-con-list {
                    font-size: 0.7rem;
                    height: 2.3rem;
                    line-height: 2.3rem;
                    padding: 0 1.3rem 0 1.5rem;
                }
                
                .tab-name{
                    width: 40%;

                }
                .tab-luck{
                    width: 22%;
                    flex-shrink: 0;
                    text-align: center;
                    position: relative;
                    span{
                        position: absolute;
                        font-size: 1rem;
                        color: #ff7a3f;
                        line-height: 1rem;
                        top:0rem;
                        right:.6rem;
                        width: 1rem;
                        height:1rem;
                        display: block;
                        transform:rotate(15deg);
                        -ms-transform:rotate(15deg); 	/* IE 9 */
                        -moz-transform:rotate(15deg); 	/* Firefox */
                        -webkit-transform:rotate(15deg); /* Safari 和 Chrome */
                        -o-transform:rotate(15deg);
                    }
                }
                .tab-bg {
                    background: #f4f6fc;
                }
                .vault-box{
                    padding: 0 1.5rem;
                    .vault-us {
                        text-align: center;
                        font-size: 0.8rem;
                        padding: 2.1rem 0 1.2rem;
                    }
                    .us-title {
                        line-height: 1.6rem;
                    }
                    .vault-num {
                        line-height: 2.6rem;
                        height: 2.6rem;
                        font-size: 2.3rem;
                        span{
                            font-size: 0.8rem;
                        }
                    }
                    .p-vault-btn {
                        display: inline-block;
                        margin: 1.2rem auto 0;
                        line-height: 2rem;
                        padding: 0 0.8rem;
                        border-radius: 4px;
                        background: #ef1e58;
                        color: #fff;
                        cursor: pointer;
                        -webkit-tap-highlight-color: rgba(0,0,0,0);
                        
                        &:hover{
                            background: #e4245b;
                        }
                    }
                    .vault-decs {
                        font-size: 0.7rem;
                        line-height: 2.1rem;
                        color: #888fa7;
                    }
                    .vault-block {
                        padding: 1rem 1.4rem 1.1rem;
                        background: #f4f6fc;
                        border-radius: 4px;
                        flex-wrap: wrap;
                        .vault-item {
                            width: 50%;
                            padding-bottom:.3rem;
                        }
                        .vault-item-title {
                            font-size: 0.7rem;
                            color: #888fa7;
                            line-height: 1.2rem;
                        }
                        .vault-item-num {
                            font-size: 1rem;
                            line-height: 1.2rem;
                            span{
                                font-size:.7rem;
                            }
                        }
                    }
                }
            }
        }
    }
    .footer{
        padding:.7rem 0;
        width: 100%;
        background: #1e2543;
        color:#5b6587;
        text-align:center;
        font-size:.7rem;
        line-height:1.4rem;

    }
    @media only screen and (max-width: 1770px) {
        .winner-detail{
            padding: 1.6rem 1rem 2.2rem;
            .winner-con .winner-int {
                .int-head{
                    padding: 0 1rem 0.8rem;
                    &::after{
                        left: 1rem;
                        right: 1rem;
                    }
                }
                .int-info .int-list{
                    padding: 0.6rem 1rem 0;
                    .int-list-num{
                        font-size: 1.6rem;
                    }
                    .int-list-decs{
                        line-height: 0.8rem;
                    }
                }
            }
             .winner-con{
                 .winner-register{
                     padding: 0.6rem 1rem 1.1rem;
                 }
                 .winner-box{
                     margin-right: 1rem;
                 }
                 .winner-end {
                    padding: 0 1rem;
                    .win-people{
                        padding: 0.4rem;
                        .win-peo-li{
                            font-size: 0.7rem;
                            .people-info{
                                width: 50%;
                            }
                        }
                        .peopel-title,.bonus-title{
                            font-size: 0.7rem;
                            margin-right: 0.2rem;
                        }
                    }
                    .winner-new .winner-new-btnBox{
                        margin-top: 1.1rem;
                    }
                 }
                 .winner-invite .invite-decs{
                        font-size: 0.7rem;
                 }
                 .winner-playedVault {
                     .playedVault-tab {
                         padding: 0 1rem;
                     }
                    .tab-name{
                        width: 50%;
                    }
                     .tab-betting{
                         width:10%;
                     }
                    .tab-luck{
                        width: 20%;
                    }
                    .tab-head{
                        padding: 0 1rem;
                    }
                    .tab-con-list{
                        padding: 0 0.8rem 0 1rem;
                    }
                    .tab-name,.tab-betting,.tab-luck{
                        flex: initial;
                    }
                    .vault-box {
                        padding: 0 1rem;
                        .vault-block{
                            padding: 1rem;
                            .vault-item{
                                width: 100%;
                                display: flex;
                                align-items: center;
                                .vault-item-num{
                                    flex: 1;
                                    text-align: right;
                                }
                                &:last-child{
                                    padding-bottom: 0;
                                }
                            }
                        }
                    }
                 }
             }
        }
    }
    @media only screen and (max-width: 1200px) {
        .footer{
            width: 1200px;
        }
    }
    @media only screen and (max-width: 800px) {
        .footer{
            width: 100%;
        }
        .register-pop .pop-con{
            left: 1rem;
            right: 1rem;
            width: auto;
            transform: translate(0,-50%);
            .register-pop-con{
                display: block;
                .register-pop-btn{
                    margin: 1.4rem auto 0;
                }
            }   
        }
        .register-pop.active .pop-con{
            transform: translate(0,-50%) scale(1,1);
        }
        .winner{
            min-width: 320px;
        }
        .winner-detail{
            padding: 0;
            min-width: 320px;
            .title{
                padding-top: 0.9rem;
                line-height: 2rem;
                display: block;
                padding-bottom: 1.5rem;
                .num{
                    font-size: 1.4rem;
                }
                .title-left{
                    padding-left: 0.3rem;
                }
                .title-icon{
                    top: 1.8rem;
                    left: 1rem;
                }
                .title-right{
                    display: block;
                    .info-text{
                        line-height: 1.2rem;
                    }
                }
            }
            .winner-con{
                display: block;
                .winner-box{
                    margin-right: 0;
                }
                .over-img{
                    width: 4.8rem;
                }
                .winner-start{
                    margin-bottom: 0.67rem;
                    padding-top: 1.2rem;
                    .betting-num-box .betting-num-title{
                        width: 1.7rem;
                    }
                }
                .winner-boxOne{
                    margin-bottom: 0.67rem;
                }
                .winner-end{
                    .winner-new{
                        padding:1rem 0 0.97rem;
                    }
                    .winner-new .betting-num-box .betting-num-title{
                        width: 1.7rem;
                    }
                    .win-people .win-peo-li{
                        font-size: 1rem;
                    }
                }
                .end-head-img{
                    height: 2.3rem;
                    img{
                        display: block;
                        height: 100%;
                    }
                }
                .winner-new{
                    padding: 0 1rem 0.97rem;
                    
                    .end-head {
                        .end-head-title{
                            font-size: 1.5rem;
                        }
                    }
                    .betting-box .betting-btn{
                        height: 1.83rem;
                        line-height: 1.83rem;
                    }
                    
                    .winner-timer{
                        display: flex;
                        align-items: center;
                        margin: 0 0;
                        position: relative;
                        .timer-title{
                            flex:1;
                            text-align: left;
                        }
                        .timer-box{
                            font-size: 1.67rem;
                        }
                        &::after {
                            display: block;
                            content: '';
                            width: 100%;
                            height: 1px;
                            position: absolute;
                            bottom: 0;
                            background: #dde0ec;
                        }
                        .timer-decs{
                            display: none;
                        }
                    }
                    .winner-new-btnBox{
                        margin-top: 0.67rem!important;
                        .tnBox-btn{
                            height: 2.4rem;
                            line-height: 2.4rem;
                            font-size: 0.8rem;
                        }
                    }
                }
                .winner-invite {
                    .invite-input{
                        height: 1.57rem;
                        line-height: 1.57rem;
                    }
                    .invite-btn{
                        height: 1.57rem;
                        line-height: 1.57rem;
                        .iconfont{
                            font-size: 0.67rem;
                        }
                    }
                }
                .winner-int{
                    margin-bottom: 0.67rem;
                    padding-top: 1rem;
                    .int-head{
                        .int-head-con{
                            padding-top: 0;
                            padding-bottom: 1rem;
                        }
                        .int-head-bottom{
                            height: auto;
                            line-height: 1.2rem;
                            padding: 0.3rem;
                            font-size: 0.7rem;
                        }
                    }
                    .int-info{
                        padding-top: 1.2rem;
                        align-items: start;
                        .int-list{
                            padding: 0 1rem;
                            min-height: initial;
                            .int-list-num{
                                font-size: 2rem;
                            }
                            .int-list-decs{
                                display: none;
                            }
                        }
                        .int-bettingBox,.int-preceding{
                            margin-top: 0;
                        }
                        .int-betting{
                            text-align: center;
                        }
                    }
                }
                .winner-playedVault {
                    min-height: initial;
                    .playedVault-tab {
                        justify-content: space-around;
                        .playedVault-item{
                            padding: 0 1.2rem!important;
                            text-align: center;
                            .pv-tab-txt{
                                display: inline-block;
                            }
                        }
                    }
                    .tab-con{
                        max-height: initial;
                        overflow: initial;
                        .tab-con-list{
                            padding: 0 1rem;
                        }
                    }
                    
                    .tab-name{
                        width: 50%;
                        span{
                            font-size: 0.8rem;
                            line-height: 1.2rem;
                            width: 2rem;
                        }
                    }
                    .tab-luck{
                        width: 20%;
                    }
                }
            }
        }
    }
</style>





